@import url(https://fonts.googleapis.com/css?family=Open+Sans:800);
 @import url(https://fonts.googleapis.com/css?family=Dancing+Script:700);
 *{
	 box-sizing: border-box;
}
.center{
	margin-top: 100px;
}
.container{
	 width: 350px;
	 height: 350px;
	 top: 28%;
	 left: 40%;
	 position: fixed;
	 /* transform: translateY(-50%) translateX(-50%); */
}
 .cube {
	 width: 26.75px;
	 height: 26.75px;
	 transform-style: preserve-3d;
	 position: absolute;
	 animation: rotation 2s infinite ease;
}
 .cube div {
	 margin: 0;
	 width: 100%;
	 height: 100%;
	 display: block;
	 position: absolute;
	 border: 2px solid #c9c8c0;
	 background: #262626;
}
 .cube .front {
	 transform: rotateY(0deg) translateZ(13.375px);
}
 .cube .back {
	 transform: rotateX(180deg) translateZ(13.375px);
}
 .cube .right {
	 transform: rotateY(90deg) translateZ(13.375px);
}
 .cube .left {
	 transform: rotateY(-90deg) translateZ(13.375px);
}
 .cube .top {
	 transform: rotateX(90deg) translateZ(13.375px);
}
 .cube .bottom {
	 transform: rotateX(-90deg) translateZ(13.375px);
}
 @keyframes rotation {
	 0% {
		 transform: rotateX(0) rotateY(0);
	}
	 100% {
		 transform: rotateX(-90deg) rotateY(90deg);
	}
}
